.fixed-expenses-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* 중앙에 배치 */
    height: 100vh; /* 전체 화면 높이 */
    background-color: #1a1a1a;
    color: white;
    padding: 20px;
}

.fixed-expense-header {
    display: flex;
    align-items: center;
    justify-content: center; /* 중앙 정렬 */
    margin-bottom: 20px;
}

.fixed-expense-header button {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
}

.fixed-expense-header h3 {
    margin: 0 10px;
}

.expense-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.expense-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.expense-item label {
    margin-right: 10px;
}

.expense-item input {
    margin-right: 10px;
    padding: 5px;
    border: none;
    border-radius: 5px;
}

.new-category-input {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.new-category-input input {
    margin-right: 10px;
    padding: 5px;
    border: none;
    border-radius: 5px;
}

.new-category-input button {
    background-color: #444;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.new-category-input button:hover {
    background-color: #666;
}

.save-button {
    background-color: #444;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.save-button:hover {
    background-color: #666;
}

.save-message {
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: #444;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 1000;
}

.fixed-expense-list {
    margin-top: 20px;
}

.fixed-expense-list h3 {
    margin-bottom: 10px;
}

.fixed-expense-list ul {
    list-style-type: none;
    padding: 0;
}