.calendar-container {
  padding: 20px;
  max-width: 900px;  /* 달력 크기를 더 키움 */
  margin: 0 auto 20px;
  background-color: #2d2d2d;
  color: white;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #444;
  padding: 10px;
  border-radius: 5px;
}

.nav-button {
  background-color: #888;
  border: none;
  padding: 5px 10px;
  color: white;
  cursor: pointer;
  border-radius: 3px;
}

.summary {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
  padding: 10px;
  background-color: #333;
  border-radius: 5px;
}

.calendar-body {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
  margin-top: 10px;
}

.day-header {
  text-align: center;
  font-weight: bold;
  background-color: #555;
  padding: 10px;
  border-radius: 5px;
}

.day-cell {
  text-align: center;
  background-color: #333;
  padding: 30px;  /* 날짜 셀 크기 키움 */
  border-radius: 5px;
  cursor: pointer;
}

.day-cell:hover {
  background-color: #444;
}

.day-cell.empty {
  background-color: transparent;
  cursor: default;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #333;
  color: white;
  padding: 20px;
  border-radius: 10px;
  z-index: 1000;
}

.popup-content {
  display: flex;
  flex-direction: column;
}

.expense-item {
  margin-bottom: 10px;
}

.new-category {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.new-category input {
  flex: 1;
  margin-right: 10px;
}

button {
  padding: 10px;
  margin-top: 10px;
  background-color: #444;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #666;
}

.save-message {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #444;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  z-index: 1000;
}

.day-number {
  position: absolute;
  top: 5px;
  left: 5px;
}

.day-total {
  position: absolute;
  bottom: 5px;
  left: 5px;
  font-size: 0.8em;
  color: #ffcc00;
}

.day-cell {
  position: relative;
}
