/* src/styles/Favorites.css */
.favorites-container {
  padding: 20px;
  background-color: #333;
  color: #fff;
  border-radius: 10px;
  max-width: 800px;
  margin: 0 auto;
  margin-top: 80px; /* 상단 마진 추가 */
}

.category {
  margin-bottom: 20px;
}

.category h3 {
  cursor: pointer;
  background-color: #444;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.favorite-items {
  margin-top: 10px;
  padding-left: 20px;
  background-color: #555;
  border-radius: 5px;
  padding: 10px;
}

.favorite-item {
  cursor: pointer;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #666;
  border-radius: 5px;
  background-color: #666;
  color: #fff;
}

.restaurant-details {
  padding: 20px;
  background-color: #222;
  color: #fff;
  border-radius: 5px;
}

.restaurant-details h2 {
  margin-bottom: 20px;
}

.restaurant-details p {
  margin-bottom: 10px;
}

.restaurant-details button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #222;
  color: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 80%;
  max-width: 600px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
}