header {
    background-color: #333;
    color: white;
    padding: 10px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
  }
  
  nav ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    padding: 0;
    margin: 0;
  }
  
  nav ul li {
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
  }
  
  nav ul li:hover {
    background-color: #444;
  }  