.mypage-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    color: white;
}

.mypage-header {
    font-size: 2em;
    margin-bottom: 20px;
    color: white;
}

.auth-buttons,
.extra-options,
.links-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 300px;
}

.auth-buttons button,
.extra-options button,
.links-section button,
.logout-button {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    background-color: #444;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.auth-buttons button:hover,
.extra-options button:hover,
.links-section button:hover,
.logout-button:hover {
    background-color: #666;
}

.fixed-expense-header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}

.fixed-expense-header button {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
}

.fixed-expense-header h3 {
    margin: 0 20px;
}

.expense-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.expense-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.expense-item label {
    width: 80px;
}

.expense-item input {
    width: 150px;
    padding: 5px;
    margin-left: 10px;
}

.expense-form input[type="text"] {
    padding: 5px;
    margin-bottom: 10px;
}

.expense-form input[type="text"],
.expense-form button {
    display: inline-block;
    margin: 5px;
}

.expense-form button {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 10px;
}

.expense-form button:hover {
    background-color: #666;
}

.fixed-expense-list {
    margin-top: 20px;
}

.fixed-expense-list ul {
    list-style-type: none;
    padding: 0;
}

.fixed-expense-list li {
    margin: 5px 0;
}

.save-message {
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: #444;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 1000;
}