body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    background-color: #181818;
    color: white;
  }
  
  .App {
    text-align: center;
  }
  
  .header {
    display: flex;
    justify-content: space-around;
    background-color: #282c34;
    padding: 10px;
  }
  
  .header button {
    background: none;
    color: white;
    border: none;
    font-size: 1em;
    cursor: pointer;
  }
  
  .header button:hover {
    text-decoration: underline;
  }
  
  .content-nav {
    display: flex;
    justify-content: center;
    margin: 10px 0;
    z-index: 2; /* 추가 */
    position: relative; /* 추가 */
  }
  
  .content-nav button {
    margin: 0 10px;
    padding: 10px;
    background-color: #444;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top:80px;
  }
  
  .content-nav button:hover {
    background-color: #666;
  }
  
  main {
    padding: 20px;
    margin-top: 10px; /* 여백을 줄여 버튼이 더 많이 보이게 조정 */
  }  