.budget-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background-color: #1a1a1a;
    color: white;
    padding: 20px;
}

.budget-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.nav-button {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
}

.nav-button:hover {
    background-color: #666;
}

.budget-form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 300px;
}

.budget-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
}

.budget-item label {
    color: white;
    width: 30%;
    text-align: right;
    padding-right: 10px;
}

.budget-item input {
    width: 60%;
    padding: 5px;
    border: none;
    border-radius: 5px;
}

.save-button {
    background-color: #444;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    max-width: 300px; /* 추가된 부분 */
    text-align: center;
}

.save-button:hover {
    background-color: #666;
}

.save-message {
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: #444;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 1000;
}