.statistics-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    width: 100%;
    background-color: #1a1a1a;
}

.stat-header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%; /* 중앙에 위치하도록 수정 */
    margin: 20px 0;
}

.stat-header button {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
}

.stat-header h2 {
    margin: 0 10px; /* 버튼과 텍스트 사이의 간격 조정 */
}

.tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.tab {
    background-color: #444;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 5px;
}

.tab:hover {
    background-color: #666;
}

.content {
    display: flex;
    justify-content: space-around;
    width: 100%;
    max-width: 1000px;
}

.chart {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.chart h3 {
    margin-bottom: 20px;
}

.chart ul {
    list-style-type: none;
    padding: 0;
}

.chart li {
    margin: 5px 0;
}