.foodrec-container {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  margin-top: 50px;
}

.foodrec-container h2 {
  margin-bottom: 20px;
}

.foodrec-container .favorite-search,
.foodrec-container .no-favorite {
  margin-top: 20px;
}

.foodrec-container input[type="text"],
.foodrec-container input[type="number"] {
  width: 45%;
  padding: 10px;
  margin: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #444;
  color: #fff;
}

.foodrec-container button {
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 5px;
  background-color: #555;
  color: #fff;
  cursor: pointer;
}

.foodrec-container button:hover {
  background-color: #666;
}

.search-results, .recommendations {
  margin-top: 20px;
}

.favorite-item, .recommendation-item {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #555;
  color: #fff;
}

.favorite-item:hover, .recommendation-item:hover {
  background-color: #666;
}